import React, { useState } from 'react';
import { MenuFoldOutlined, MenuUnfoldOutlined } from '@ant-design/icons';

import {
  ConfigProvider,
  Button,
  Layout as AntdLayout,
  Menu,
  theme,
} from 'antd';

import FooterContent from '../components/footer';

import themeConfig from './themeConfig';
import s from './index.module.scss';
import { AppConfig, AppConfigParams } from '../shared/config/app';
import { MonacoEditor } from '../shared/config/editor';

import { menu } from '../shared/menu';
import { Outlet } from '@modern-js/runtime/router';

const { Header, Sider, Content, Footer } = AntdLayout;

export default function Layout() {
  const [collapsed, setCollapsed] = useState(false);
  const [appConfig, setAppConfig] = useState<
    Partial<AppConfigParams> | undefined
  >({
    language: 'json',
  });
  const [editor, setEditor] = useState(undefined);

  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();

  return (
    <ConfigProvider theme={themeConfig}>
      <AppConfig.Provider
        value={{
          ...appConfig,
          setAppConfig,
        }}
      >
        <MonacoEditor.Provider
          value={{
            editor,
            setEditor,
          }}
        >
          <AntdLayout className={s.layout}>
            <Header
              style={{
                color: '#fff',
                height: 64,
                paddingInline: 24,
                lineHeight: '64px',
                background: colorBgContainer,
                display: 'flex',
                justifyContent: 'space-between',
              }}
            >
              <div className={s.logoContainer}>
                <img
                  width={44}
                  height={44}
                  src="https://rain120.github.io/study-notes/img/chao.png"
                  alt=""
                  className={s.logo}
                />
                <div className={s.slogan}>Chaos</div>
              </div>
            </Header>
            <AntdLayout>
              <Sider trigger={null} collapsible collapsed={collapsed}>
                <Menu
                  mode="inline"
                  theme="dark"
                  defaultOpenKeys={menu.map(m => m.key)}
                  inlineCollapsed={false}
                  items={menu}
                />
                <div className={s.menuOp}>
                  <Button
                    type="text"
                    icon={
                      collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />
                    }
                    onClick={() => setCollapsed(!collapsed)}
                    style={{
                      fontSize: '16px',
                    }}
                  />
                </div>
              </Sider>
              <AntdLayout>
                <Content
                  style={{
                    margin: 16,
                    padding: 24,
                    minHeight: 280,
                    background: colorBgContainer,
                    borderRadius: borderRadiusLG,
                  }}
                >
                  <Outlet></Outlet>
                </Content>
                <Footer
                  style={{
                    textAlign: 'center',
                  }}
                >
                  <FooterContent />
                </Footer>
              </AntdLayout>
            </AntdLayout>
          </AntdLayout>
        </MonacoEditor.Provider>
      </AppConfig.Provider>
    </ConfigProvider>
  );
}
